<template>
  <el-upload
    ref="upload"
    :name="data.name || 'file'"
    :data="data"
    :http-request="uploadFile"
    :action="action"
    :auto-upload="autoUpload"
    :on-success="uploadSuccess"
    :before-upload="beforeUpload"
    :on-remove="handleRemove"
    :limit="uploadLimit"
    :show-file-list="uploadControls.show_file_list"
    :file-list="fileList"
    :list-type="listType">
    <!--todo:-->
    <el-avatar v-if="uploadControls.button_type === 'avatar'" :src="avatarImage.avatar_url" :alt="avatarImage.username"
               fit="cover" :size="avatarImage.size"></el-avatar>
    <!--todo:-->
    <el-button size="small" type="primary" v-if="uploadControls.button_type === 'picture'"></el-button>
    <!--todo:-->
    <i class="el-icon-plus" v-if="uploadControls.button_type === 'card'"></i>
    <!--todo:-->
    <span v-if="uploadControls.show_tips" class="el-upload__tip"
          style="margin-left: 20px;">{{ tips ? tips : `${imgWidth}*${imgHeight}jpg/png ` }}</span>
  </el-upload>
  <!--todo:-->
  <el-button v-if="!autoUpload" style="margin-top: 20px" plain type="primary" size="medium" @click="submitUpload">
    
  </el-button>
</template>

<script>

import func from '@/utils/func';
import URLS from '@/api/urls';
import $http from '@/tools/request';

export default {
  name: 'CommonUpload',
  props: {
    /*  */
    fileList: {
      type: Array,
      default: () => []
    },
    /*  */
    imgWidth: {
      type: Number,
      default: () => 320
    },
    /*  */
    imgHeight: {
      type: Number,
      default: () => 250
    },
    /*  */
    fileSize: {
      type: Number,
      default: () => 1024
    },
    /*  */
    uploadSuccess: {
      type: Function
    },
    /*  */
    handleRemove: {
      type: Function
    },
    /*  */
    uploadLimit: {
      type: Number,
      default: () => 1000
    },
    /*  */
    tips: {
      type: String,
      default: ''
    },
    /*  */
    listType: {
      type: String,
      default: () => 'picture'
    },
    /*  */
    action: {
      type: String,
      default: () => URLS.baseURL + URLS.file.upload
    },
    /*  */
    autoUpload: {
      type: Boolean,
      default: () => true
    },
    /*  */
    avatarImage: {
      type: Object,
      default() {
        return { avatar_url: '', username: '', size: 100 };
      }
    },
    /*  */
    data: {
      type: Object,
      default() {
        return { name: 'file', round_name: true, file: {} };
      }
    },
    /*  */
    uploadControls: {
      type: Object,
      default() {
        return { button_type: 'picture', show_tips: true, show_file_list: true };
      }
    }
  },
  methods: {
    /**
     * todo:
     */
    submitUpload() {
      this.$refs.upload.submit();
    },
    /**
     * todo:
     * @param file
     */
    uploadFile(file) {
      /* todo:form */
      let param = new FormData();
      param.append('file', file.file);
      param.append('filename', file.file.name);
      param.append('token', this.$store.state.token || window.localStorage.getItem('token'));
      param.append('round_name', this.data.round_name || false);
      param.append('file_type', 'image');
      /* todo: */
      if (!this.data.round_name) {
        param.append('path', this.data.file.path.replace(this.data.file.filename, ''));
      }
      /* todo: */
      let config = { headers: { 'Content-Type': 'multipart/form-data', 'Authorization': this.$store.state.token } };
      $http.post(file.action, param, config).then(response => {
        response.data.filename = file.filename;
        file.onSuccess(response.data);
      }).catch(() => {
        file.onError();
      });
    },
    /**
     * todo
     * @param file
     */
    getBackgroundColor(file) {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (e) => {
        const image = new Image();
        image.src = e.target.result;
        image.onload = () => {
          const canvas = document.getElementById('s-award-canvas');
          const ctx = canvas.getContext('2d');
          ctx.drawImage(image, 0, 0, this.imgWidth, this.imgHeight);
          this.$emit('setBackgroundColor', func.rgbToHex(ctx.getImageData(0, 0, image.width, image.height).data));
        };
      };
    },
    /**
     * todo:
     * @param file
     * @returns {Promise<*>|boolean}
     */
    beforeUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/jpg';
      const isLt30KB = file.size / 1024 / this.fileSize < 1;
      if (!isJPG) {
        this.$message.error(' jpgpng !');
        return false;
      }
      if (!isLt30KB) {
        this.$message.error(`${this.fileSize}KB!`);
        return false;
      }
      if (!this.imgHeight && !this.imgWidth) {
        return isJPG && isLt30KB;
      }
      let __this = this;
      const isSize = new Promise(function (resolve, reject) {
        let width = __this.imgWidth;
        let height = __this.imgHeight;
        let _URL = window.URL || window.webkitURL;
        let image = new Image();
        image.onload = function () {
          let valid = (image.width === width && image.height === height)
            || (image.width >= width - 10 && image.height >= height - 10 && image.width <= width + 10 && image.height <= height + 10);
          valid ? resolve() : reject();
        };
        image.src = _URL.createObjectURL(file);
      }).then(() => {
          return file;
        }, () => {
          __this.$message.error(`,${__this.imgWidth}*${__this.imgHeight}!`);
          return Promise.reject();
        }
      );
      return isJPG && isLt30KB && isSize;
    }
  }
};
</script>

